.MainBackground{
    width: 100%;
    height: 100vh;
    overflow-y: hidden;
}

.row{
    display: flex;
    flex-direction: row;
}

.column{
    display: flex;
    flex-direction: column;
}

.icons{
    width: 56px;
    height: 56px;
}

.button-history{
    margin-left: 14px;
    width: 248px;
    height: 56px;
    border-radius: 18px;
    border-color: white;
    color: white;
    border-width: 1px;
    border-style: solid;
    justify-content: center;
    align-items: center;
    display: flex;
    cursor: pointer;
}

.green-button{
    color: #32FF2E;
    border-color: #32FF2E;
}

.button-text{
    font-family: 'Exo', sans-serif;
    user-select: none;
    font-weight: 500;
    font-size: 24px;
}

.big-avatar{
    width: 364px;
    height: 364px;
    pointer-events: none;
}

.nickname{
    color: white;
    font-family: Exo, sans-serif;
    font-size: 39px;
    font-weight: 500;
    text-align: center;
    margin-top: 36px;
}

.add-money{
    width: 56px;
    height: 56px;
    margin-left: -56px;
    margin-top: 1px;
    cursor: pointer;
}

.yellow-button{
    color: #FFDE2E;
    border-color: #FFDE2E;
}

@media only screen and (min-width: 200px) and (max-width: 800px) {

    .big-avatar{
        width: 200px;
        height: 200px;
        pointer-events: none;
        margin-top: 10px;
    }

    .nickname{
        color: white;
        font-family: Exo, sans-serif;
        font-size: 25px;
        font-weight: 500;
        text-align: center;
        margin-top: 20px;
    }

    .button-history{
        margin-left: 0;
        width: 248px;
        height: 56px;
        border-radius: 18px;
        border-color: white;
        color: white;
        border-width: 1px;
        border-style: solid;
        justify-content: center;
        align-items: center;
        display: flex;
        cursor: pointer;
    }

    .green-button{
        color: #32FF2E;
        border-color: #32FF2E;
    }

    .yellow-button{
        color: #FFDE2E;
        border-color: #FFDE2E;
    }
}